import React, { useState } from "react";
import "../../css/board/BoardDetail.css";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import Header from "../layout/Header";
import Footer from "../layout/Footer";


const BoardDetail = () => {
    const navigate = useNavigate();
    const currentUser = "wjdwodnr"; //    ID

//    
    const applicants = [
        { id: 1, name: "", phone: "010-1234-5678", date: "2025-01-10" },
        { id: 2, name: "", phone: "010-2345-6789", date: "2025-01-12" },
        { id: 3, name: "", phone: "010-3456-7890", date: "2025-01-15" },
    ];

    const boards = [
        {
            id: 1,
            title: "  ",
            author: "wjdwodnr",
            serviceStat: "",
            serviceDate: "2025-01-11",
            serviceTime: "6",
            category: "",
            date: "2025-01-11",
            deadlineStat:"",
            deadlineDate: "2025-01-20",
            content: "1",
            numberPeople: "5",
            thumbnail: "/images/thumbnail.png"
        }
    ];

    const { id } = useParams();
    const [boardsState, setBoardsState] = useState(boards);
    const board = boardsState.find((b) => b.id === parseInt(id, 10));
    const [serviceStat, setServiceStat] = useState(board.serviceStat);
    const [isModalOpen, setIsModalOpen] = useState(false); //   

    if (!board) {
        return <p>   .</p>;
    }

    const handleStatusChange = (e) => {
        const updatedBoards = boardsState.map((b) =>
            b.id === board.id ? { ...b, serviceStat: e.target.value } : b
        );
        setBoardsState(updatedBoards);
    };

    //    
    const boardDetailEditPage = (id) => {
        navigate("/boards/"+id+"/edit")
    }

    //   
    const boardStatBtn = (stat) => {
        setServiceStat(stat);
        alert(stat);
    }

    const showServicePeople = () => {
        setIsModalOpen(true); //  
    };

    const closeModal = () => {
        setIsModalOpen(false); //  
    };

    return (
        <div>
            <Header />
            <div className="board-detail-page">
                <div className="board-header">
                    <h1 className="board-title">{board.title}</h1>
                    <div className="board-service-people">
                        <button className="board-service-people-button" onClick={() => showServicePeople()}> </button>
                    </div>
                    <div className="board-meta">
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.author}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.numberPeople}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.serviceTime}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.category}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.serviceStat}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.date}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.deadlineStat}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.deadlineDate}</span>
                        </div>
                        <div className="board-item">
                            <span className="board-label"></span>
                            <span className="board-value">{board.date}</span>
                        </div>
                    </div>
                </div>

                <div className="board-thumbnail-container">
                    <img src={board.thumbnail} alt={`${board.title} `} className="board-thumbnail"/>
                </div>

                <div className="board-content">
                    <p>{board.content}</p>
                </div>


                <div className="board-footer">

                    {board.author === currentUser ? (
                        <button className="board-editor-button" onClick={() => boardDetailEditPage(id)}>
                            
                        </button>
                    ) : (
                        <button className="board-apply-button" onClick={() => alert("  ")}>
                            
                        </button>
                    )}

                    {board.author === currentUser && (serviceStat != "" ? (
                        <div className="board-stat-div">
                            <button className="board-stat-button" onClick={() => boardStatBtn("")}>
                                
                            </button>
                        </div>

                    ) : (
                        <div className="board-stat-div">
                            <button className="board-stat-button" onClick={() => boardStatBtn("")}>
                                
                            </button>
                        </div>
                    ))}

                    <button className="back-button" onClick={() => window.history.back()}>
                        
                    </button>
                </div>

                {/*  */}
                {isModalOpen && (
                    <div className="modal-overlay">
                        <div className="modal-content">
                            <h2> </h2>
                            <div className="applicant-list">
                                {applicants.map((applicant) => (
                                    <div key={applicant.id} className="applicant-row">
                                        <p><strong>:</strong> {applicant.name}</p>
                                        <p><strong>:</strong> {applicant.phone}</p>
                                        <p><strong>:</strong> {applicant.date}</p>
                                    </div>
                                ))}
                            </div>
                            <button className="close-modal" onClick={closeModal}></button>
                        </div>
                    </div>
                )}

            </div>
            <Footer/>
        </div>
    );
};

export default BoardDetail;
