import React, { useState } from "react";
import "../../css/user/UserProfile.css";
import Header from "../layout/Header";
import Footer from "../layout/Footer";

const UserProfile = () => {
    const [user, setUser] = useState({
        name: "",
        email: "hong@example.com",
        phone: "010-1234-5678",
        serviceTime: "30",
    });

    const applications = [
        { id: 1, title: "  ", date: "2025-01-10" },
        { id: 2, title: "  ", date: "2025-01-12" },
        { id: 3, title: "  ", date: "2025-01-15" },
    ];

    const posts = [
        { id: 1, title: "  ", date: "2025-01-05" },
        { id: 2, title: "  ", date: "2025-01-07" },
        { id: 3, title: "  ", date: "2025-01-09" },
    ];

    const [showApplications, setShowApplications] = useState(false); //   
    const [showMyPostsModal, setShowMyPostsModal] = useState(false); //      

    const handleEdit = () => {
        alert("   .");
    };

    const toggleApplications = () => {
        setShowApplications(!showApplications); //  
    };


    const toggleMyPostsModal = () => {
        setShowMyPostsModal(!showMyPostsModal); //      
    };

    return (
        <div>
            <Header />
            <div className="user-profile-container">
                <h2 className="profile-title"> </h2>

                <div className="profile-card">
                    <div className="profile-item">
                        <label>:</label>
                        <span>{user.name}</span>
                    </div>

                    <div className="profile-item">
                        <label>:</label>
                        <span>{user.email}</span>
                    </div>

                    <div className="profile-item">
                        <label>:</label>
                        <span>{user.phone}</span>
                    </div>

                    <div className="profile-item">
                        <label>:</label>
                        <span>{user.serviceTime}</span>
                    </div>

                    <button className="edit-btn" onClick={handleEdit}> </button>

                    {/*   */}
                    <button className="applications-btn" onClick={toggleApplications}>
                        {showApplications ? " " : " "}
                    </button>

                    {/*     */}
                    <button className="my-posts-btn" onClick={toggleMyPostsModal}>
                           
                    </button>

                </div>

                {/*   */}
                {showApplications && (
                    <div className="applications-list">
                        <h3>  </h3>
                        <ul>
                            {applications.map((app) => (
                                <li key={app.id} className="application-item">
                                    <span>{app.title}</span>
                                    <span>({app.date})</span>
                                </li>
                            ))}
                        </ul>
                    </div>
                )}

                {/*     */}
                {showMyPostsModal && (
                    <div className="modal">
                        <div className="modal-content">
                            <span className="close-btn" onClick={toggleMyPostsModal}>&times;</span>
                            <h3>   </h3>
                            <ul className="posts-list">
                                {posts.map((post) => (
                                    <li key={post.id} className="post-item">
                                        <span>{post.title}</span>
                                        <span>({post.date})</span>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    </div>
                )}

            </div>
            <Footer />
        </div>
    );
};

export default UserProfile;
