import React, { useState, useEffect } from "react";
import "../../css/board/BoardWrite.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Header from "../layout/Header";
import Footer from "../layout/Footer";

const BoardEdit = ({ postId }) => {
    const [title, setTitle] = useState("");
    const [category, setCategory] = useState("tech");
    const [content, setContent] = useState("");
    const [numberPeople, setNumberPeople] = useState("");
    const [deadline, setDeadline] = useState(new Date());
    const [serviceDate, setServiceDate] = useState(new Date());
    const [thumbnail, setThumbnail] = useState(null);
    const [serviceTime, setServiceTime] = useState("");

    //     (: API )
    useEffect(() => {
        //   API    ( static  )
        const fetchPostData = async () => {
            const postData = {
                title: "  ",
                category: "tech",
                content: "  ",
                numberPeople: 5,
                deadline: new Date(),
                serviceDate: new Date(),
                thumbnail: null, //    URL
            };
            setTitle(postData.title);
            setCategory(postData.category);
            setContent(postData.content);
            setNumberPeople(postData.numberPeople);
            setDeadline(postData.deadline);
            setServiceDate(postData.serviceDate);
            setThumbnail(postData.thumbnail);
        };
        fetchPostData();
    }, [postId]);

    const handleThumbnailChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setThumbnail(URL.createObjectURL(file));
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        //     (  )
        console.log({
            title,
            category,
            content,
            numberPeople,
            deadline,
            serviceDate,
            thumbnail,
        });
        alert(" !");
    };

    return (
        <div>
            <Header />
            <div className="borad-write-container">
                <h2> </h2>
                <form onSubmit={handleSubmit} className="board-write-form">
                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeTitle"></label>
                        <input
                            type="text"
                            id="writeTitle"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            placeholder="  "
                            required
                        />
                    </div>

                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeCategory"></label>
                        <select
                            id="writeCategory"
                            value={category}
                            onChange={(e) => setCategory(e.target.value)}
                        >
                            <option value="tech"></option>
                            <option value="life"></option>
                            <option value="news"></option>
                        </select>
                    </div>

                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeNumberPeople"></label>
                        <input
                            type="number"
                            id="writeNumberPeople"
                            value={numberPeople}
                            onChange={(e) => setNumberPeople(e.target.value)}
                            placeholder=" "
                            required
                        />
                    </div>

                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeServiceTime"></label>
                        <input
                            type="number"
                            id="writeServiceTime"
                            value={serviceTime}
                            onChange={(e) => setServiceTime(e.target.value)}
                            placeholder="  "
                            required
                        />
                    </div>


                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeDeadline"></label>
                        <DatePicker
                            selected={deadline}
                            onChange={(date) => setDeadline(date)}
                            dateFormat="yyyy-MM-dd HH:mm" //   
                            showTimeSelect //   
                            timeFormat="HH:mm" //    (24 )
                            timeIntervals={15} //   (15 )
                            timeCaption="" //   
                            placeholderText="  "
                            id="writeDeadline"
                            required
                        />
                    </div>

                    {/*   */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeServiceDate"></label>
                        <DatePicker
                            selected={serviceDate}
                            dateFormat="yyyy-MM-dd HH:mm" //   
                            showTimeSelect //   
                            timeFormat="HH:mm" //    (24 )
                            timeIntervals={15} //   (15 )
                            timeCaption="" //   
                            placeholderText="  "
                            id="writeServiceDate"
                            required
                        />
                    </div>

                    {/*  */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeContent"></label>
                        <textarea
                            id="writeContent"
                            value={content}
                            onChange={(e) => setContent(e.target.value)}
                            placeholder="  "
                            required
                        />
                    </div>

                    {/*   */}
                    <div className="borad-write-form-group">
                        <label htmlFor="writeThumbnail"></label>
                        <input
                            type="file"
                            id="writeThumbnail"
                            accept="image/*"
                            onChange={handleThumbnailChange}
                        />
                        {thumbnail && (
                            <div className="thumbnail-preview">
                                <img src={thumbnail} alt=" " />
                            </div>
                        )}
                    </div>

                    {/*   */}
                    <div className="board-write-form-actions">
                        <button type="submit" className="submit-btn"></button>
                    </div>
                </form>
            </div>
            <Footer />
        </div>
    );
};

export default BoardEdit;
