// BoardList.js
import React, { useState } from "react";
import "../../css/board/Board.css";
import { useNavigate } from "react-router-dom";

const BoardList = () => {

    const posts = [
        { id: 1, title: "React  ", author: "John Doe", category:"", serviceStat:"", serveiceDate:"2025-01-01", serveiceTime:"6",  recruitStat:"", recruitDeadline:"2025-01-03",  date: "2025-01-01", thumbnail: "/imges/thumbnail.png"},
        { id: 2, title: "React  ", author: "John Doe", category:"", serviceStat:"", serveiceDate:"2025-01-01", serveiceTime:"6",  recruitStat:"", recruitDeadline:"2025-01-03",  date: "2025-01-01", thumbnail: "/imges/thumbnail.png"},
        { id: 3, title: "JavaScript   ", author: "Jane Smith", category:"",serviceStat:"",serveiceDate:"2025-01-01",serveiceTime:"6", recruitStat:"", recruitDeadline:"2025-01-03",   date: "2025-01-02", thumbnail: "/imges/thumbnail.png"},
        { id: 4, title: "Next.js  ", author: "Alice Brown", category:"", serviceStat:"", serveiceDate:"2025-01-01",serveiceTime:"6",  recruitStat:"", recruitDeadline:"2025-01-03", date: "2025-01-03", thumbnail: "/imges/thumbnail.png"}
    ];

    const [category, setCategory] = useState("all");
    const [boardStat , setBoardStat] =  useState("all");
    const [searchQuery, setSearchQuery] = useState("");
    const [boardReruit, setBoardRecruitStat] = useState("all");

    //    
    const filteredPosts = posts.filter((post) => {
        const matchesCategory =
            category === "all" || post.category === category;
        const matchesSearch = post.title
            .toLowerCase()
            .includes(searchQuery.toLowerCase());
        return matchesCategory && matchesSearch;
    });

    const navigate = useNavigate();

    //    
    const boardWritePage = () => {
        navigate("/boards/write")
    }

    return (
        <div className="boards-list">
            <h2> </h2>

            {/*     */}
            <div className="filters">
                <div className="filters-left">
                    <div className="boards-stat-div">
                        <span>  : </span>
                        <select
                            value={boardStat}
                            onChange={(e) => setBoardStat(e.target.value)}
                            className="board-stat-select filters-select"
                        >
                            <option value="all"></option>
                            <option value="before_starting"></option>
                            <option value="starting"></option>
                            <option value="finish"></option>
                        </select>
                    </div>
                    <div className="boards-recruit-stat-div">
                        <span>  : </span>
                        <select value={boardReruit}   onChange={(e) => setBoardRecruitStat(e.target.value)}  className="board-recruit-stat-select filters-select">
                            <option value="all"></option>
                            <option value="recruiting"></option>
                            <option value="recruiting_deadline"></option>
                        </select>
                    </div>
                    <div className="category-div">
                        <span> : </span>
                        <select
                            value={category}
                            onChange={(e) => setCategory(e.target.value)}
                            className="category-select filters-select"
                        >
                            <option value="all"></option>
                            <option value="environmental"></option>
                            <option value="social_welfare"></option>
                            <option value="education"></option>
                            <option value="culture_and_art"></option>
                            <option value="animal_protection"></option>
                        </select>
                    </div>
                    <div className="search-div">
                        <label htmlFor="searchId"> : </label>
                        <input
                            type="text"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            placeholder=" "
                            id="searchId"
                            className="search-input"
                        />
                    </div>
                </div>
                <div className="filters-right">
                    <div className="boards-btn-div">
                        <button type="button" className="board-create-button" onClick={boardWritePage}>
                            
                        </button>
                    </div>
                </div>
            </div>

            {/*   */}
            <div className="boards-cards">
                {filteredPosts.map((post) => (
                    <div key={post.id} className="boards-card">
                        <img
                            src={post.thumbnail}
                            alt={`${post.title} `}
                            className="boards-thumbnail"
                        />
                        <div className="boards-info">
                            <h3 className="boards-title">
                                <a href={`/boards/${post.id}`}>{post.title}</a>
                            </h3>
                            <div className="boards-meta">
                                <p className="boards-author">: {post.author}</p>
                                <p className="boards-category">: {post.category}</p>
                            </div>
                            <div className="boards-details">
                                <div className="details-row">
                                    <p className="boards-service-stat"><strong>:</strong> {post.serviceStat}</p>
                                    <p className="boards-recruit-stat"><strong>:</strong> {post.recruitStat}</p>
                                </div>
                                <div className="details-row">
                                    <p className="boards-service-date"><strong>:</strong> {post.serveiceDate}</p>
                                    <p className="boards-recruit-deadline"><strong>:</strong> {post.recruitDeadline}</p>

                                </div>
                                <div className="details-row">
                                    <p className="boards-service-time"><strong>:</strong> {post.serveiceTime}</p>
                                    <p className="boards-date"><strong>:</strong> {post.date}</p>
                                </div>
                            </div>
                        </div>
                    </div>

                ))}
            </div>


            {/*      */}
            {filteredPosts.length === 0 && (
                <p className="no-results">  .</p>
            )}
        </div>
    );
};

export default BoardList;
